<template>
	<view class="zl-box">
		<view class="box-one">
			<view class="title"><text>{{datas.title}}</text></view>
			<view class="text"><text>{{datas.remark}}</text></view>
			<view><text>成色：{{datas.texture}}</text></view>
			<view class="weight"><text>克重：{{datas.min_g + '-' + datas.max_g}}</text></view>
		</view>
		<view class="box-two">
			<u-checkbox-group  v-for="(item,index) in datas.album" :key="index">
				<view style="display: flex;justify-content: space-between;flex-wrap: wrap;">
					<view class="photo-box" v-if="index != 0" >
						<view class="photo">
							<image :src="item.img0" mode="aspectFill"></image>
						</view>
						<view class="checkbox">
							<u-checkbox v-model="item.checked" @change="checkboxChange($event,index)" shape="circle" active-color="#2d407a"></u-checkbox>
						</view>
					</view>
				</view>
			</u-checkbox-group>
		</view>
		<view class="bottom-box">
			<view class="min-box" @click="share(0)">
				<image src="../../static/index/haoyou.png" mode="aspectFill"></image>
				<view style="margin-top: 6upx;">微信好友</view>
			</view>
			<view class="min-box" @click="share(1)">
				<image src="../../static/index/pyq.png" mode="aspectFill"></image>
				<view style="margin-top: 6upx;">朋友圈</view>
			</view>
			<view class="min-box" @click="share(2)">
				<image src="../../static/index/lianjie.png" mode="aspectFill"></image>
				<view style="margin-top: 6upx;">分享链接</view>
			</view>
			<view class="min-box" @click="share(3)">
				<image src="../../static/index/save.png" mode="aspectFill"></image>
				<view style="margin-top: 6upx;">生成海报</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: '婚嫁双喜龙凤套链',
				text: '设计师将龙凤呈祥与花卉、双喜元素结合，寓意佳偶天成，天生一对，婚后生活幸福美满',
				purity: '足金999.9',
				weight: '18.16g-18.41g',
				checked:false,
				datas:'',
				array:[],//处理数据
			}
		},
		onLoad(e) {
			// console.log(JSON.parse(e.data))
			
			JSON.parse(e.data).album.forEach(a=>{
				a.checked = false
			})
			this.datas = JSON.parse(e.data)
			this.member = uni.getStorageSync('member_info')
		},
		methods: {
			checkboxChange(e,ind) {
				console.log(e,ind)
				this.array = []
				this.datas.album.forEach((a,index)=>{
					if(index != 0){
						this.array.push(a)
					}
				})
				this.datas.album[e].checked = e.value
				// console.log(array)
			},
			share(e){
				let that = this
				let data = this.datas.title+"\n"+this.datas.remark+'\n'+'成色：'+this.datas.texture+'\n'+'克重：'+this.datas.min_g + '-' + this.datas.max_g
				console.log(data)
				if(e == 0 || e == 1){
					uni.setClipboardData({
					    data: data,
					    success: function () {
							uni.showToast({
								title:'文字已复制到剪贴板',icon:'none'
							})
					    }
					});
					let num = 0 
					let arr = []
					this.array.forEach(b=>{
						if(b.checked == true){
							arr.push(b)
							uni.saveImageToPhotosAlbum({
								filePath: b.img0,
								success: function () {
									num ++
									if(arr.length == num){
										uni.showModal({
											title:'图片已保存，是否打开微信？',
											success(mod) {
												if(mod.confirm){
													plus.runtime.openURL("weixin://")
												}
											}
										})
										
									}
								}
							})
						}
					})
				}else if(e == 2){
					//type 1 商品 0 首页
					let data = {
						id:this.datas.id,
						type:1,
						name:this.member.id
					}
					uni.share({
					    provider: "weixin",
					    scene: "WXSceneSession",
					    type: 0,
					    href: 'http://zhuanshi.nxm.wanheweb.com/smsj/index.html#/pages/index/index?data='+JSON.stringify(data),
					    title: this.datas.title,
					    summary: this.datas.remark,
					    imageUrl: this.datas.avatar,
					    success: function (res) { 
							// console.log(res) 
							
					    },fail: function (err) {
							// console.log(err)
					        // that.com.msg('失败')
					    }
					});
				}
			}
		}
	}
</script>

<style>
	page {
		background-color: #F6F6F6;
	}
</style>

<style lang="scss">
	.zl-box {
		padding: 30upx;

		.box-one {
			font-size: 26upx;

			.text {
				margin: 10upx 0;
			}

			.weight {
				margin-top: 10upx;
			}
		}

		.box-two {
			margin-top: 40upx;
			
			.photo-box{
				position: relative;
				.photo {
					margin-bottom: 10upx;
					image {
						width: 220upx;
						height: 220upx;
						border-radius: 10upx;
						background-color: #F1F1F1;
					}
				}
				.checkbox {
					position: absolute;
					right: -24upx;
					top: 0;
				}
			}
		}
	}
	.bottom-box{
		width: 100%;height: 180upx;
		padding: 0 50upx;
		display: flex;align-items: center;justify-content: space-between;
		position: fixed;bottom: 0;left: 0;z-index: 998;
		.min-box{
			text-align: center;
			font-size: 22upx;
			color: #666666;
			image{
				width: 80upx;
				height: 80upx;
			}
		}
	}
</style>
